* {
    padding: 0px;
}

:root {
    --padding10px: 10px;
}

.dialogs {
    padding: var(--padding10px);
    display: grid;
    grid-template-columns: 2fr 10fr;
    /*grid-template-rows: 10fr 2fr;*/
}

.dialogItem {
    padding: 0px 0px 20px 10px;
}

.dialogItem a {
    text-decoration: none;
    transition: 0.25s;
}

.dialogItem a.active {
    color: white;
    border-radius: 15%;
    padding: 2px 10px 2px 5px;
}

.dialogItem a:hover {
    color: gold;
    border-radius: 15%;
    padding: 2px 10px 2px 5px;
}

.messages {
    padding: var(--padding10px);
}

.message {
    padding: 2px 10px 12px 5px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-left: 2px solid grey;
}

.messageItem {
    border: 3px solid gold;
    border-image: url(./../assets/border.png) 25 stretch;
    border-radius: 20px;
    display: inline;
    padding: 0px 10px 0px 10px;
    width: 300px;
    overflow: hidden;
}

.messageImage {
    /*border: 3px solid gold;*/
    border-radius: 20px;
    display: inline;
    padding: 0px 10px 0px 10px;
}

.messageLogo {
    width: 70px;
    height: 70px;
    border-radius: 50%;
}

.messageAdd {
    display: flex;
    flex-direction: column;
    width: 300px;
}

